import React, { Component } from 'react';
import Loading from '../../components/Loading';
import './styles.less';
import { fetchRandomData } from '../../services';
import Gallery from '../../components/Gallery';

class RandomMM extends Component {

  state = {
    data: [],
  };

  componentDidMount() {
    this.fetchData();
  }

  fetchData = () => {

    if (Loading.isLoading()) {
      return;
    }

    Loading.globalLoading();

    
    fetchRandomData()
      .then((res) => {
        let dataArr = [];
        console.log(res)
        for(let i in res){
          for(let j = 0;j<res[i].length;j++){
            dataArr.push(res[i][j]);
          }
        }
        const data = dataArr.map((item) => {
          return {
            // fileName: item._id,
            // title: item.who,
            // desc: item.desc,
            // imageURL: item.url,
            fileName: item.name,
            title: item.name,
            desc: item.name,
            imageURL: item.imgUrl,
          };
        })

        this.setState({
          data,
        });
        console.log(res.results);
      })
      .then(() => {
        Loading.stopGlobalLoading();
      }, (err) => {
        console.error(err);
        Loading.stopGlobalLoading();
      });

  }

  render() {
    return (
      <div className="random-mm-wrap">
        <div className="refresh" onClick={this.fetchData}>
          Refresh
        </div>
        <div className="main">
          <Gallery imageDatas={this.state.data} />
        </div>
        {/* <pre dangerouslySetInnerHTML={{ __html: JSON.stringify(this.state.data, null, 2) }} /> */}
      </div>
    );
  }
}

export default RandomMM;



